import React from "react";
import Navbar from "../sidecomponents/navbar";
import HomeBody from "../sidecomponents/Homebody";
import Spinner from "../sidecomponents/Spinner";
import { useState , useEffect } from "react";
import Slider from "../sidecomponents/slider";
import Footer from "../sidecomponents/footer";

const Home = ()=> {
    const [loading, setloading] = useState(true);
    const Images = ["./img/Sliderimage7.png","./img/Sliderimage5.png","./img/Sliderimage6.png","./img/Sliderimage2.png","./img/Sliderimage3.png"];
    const Title = "Member Area";
    const Description = "The member area of a fitness website is a dynamic and interactive platform designed to provide an enhanced experience for individuals committed to their health and wellness journeys. Within this dedicated space, members gain access to a wealth of exclusive resources and tools tailored to support their fitness goals.";
    const dataloading = ()=> {
        window.addEventListener('DOMContentLoaded', function () {
            setloading(false);
          })
    };
    useEffect(()=>{
        dataloading();
    },[]);

    return (
        <>
            {/* {loading && <Spinner/>} */}
            <Navbar/>
            <HomeBody/>
            <Slider Title={Title} Description={Description} Images={Images}/>
            <Footer/>
        </>
    );
};

export default Home;