import About from "./components/maincomponents/About";
import Membership from "./components/maincomponents/Membership";
import Services from "./components/maincomponents/Services";
import Home from "./components/maincomponents/Home";
import Facilities from "./components/maincomponents/Facilities";
import { Routes, Route, Navigate } from "react-router-dom";
import { useState } from "react";
import Login from "./components/sidecomponents/Login";

function App() {
  const [move, setMove] = useState(true); 
  // original value false

  const pull_data = (data) => {
    setMove(true);
  }

  return (
    <>
      <Routes>
        <Route exact path="/" element={<Login func={pull_data}/>} />
        { move && <>
        <Route exact path="/home" element={<Home />} />
        <Route exact path="/about" element={<About />} />
        <Route exact path="/services/*" element={<Services />} />
        <Route exact path="/membership" element={<Membership />} />
        <Route exact path="/facilities" element={<Facilities />} /> </> }
      </Routes>
    </>
  );
}

export default App;
