import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

export default function Login(props) {
  const [name, setName] = useState("");
  const [signupemail, setSignupmail] = useState("");
  const [loginemail, setLoginupmail] = useState("");
  const [signuppassword, setSignuppassword] = useState("");
  const [loginpassword, setLoginpassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    let signin = document.getElementById("signIn");
    let gotosignup = document.getElementById("gotoSignup");
    signin.style.display = "none";
    gotosignup.style.display = "none";
  }, []);

  const switchMenu = (e) => {
    let signin = document.getElementById("signIn");
    let gotosignup = document.getElementById("gotoSignup");
    let signup = document.getElementById("signUp");
    let gotosignin = document.getElementById("gotoSignin");
    let logintopdiv = document.getElementById("logintopDiv");
    let innerlogindiv = document.getElementById("innerloginDiv");
    let loginbtn = document.getElementById("loginBtn");
    let registerbtn = document.getElementById("registerBtn");
    let vw = Math.max(
      document.documentElement.clientWidth || 0,
      window.innerWidth || 0
    );

    if (
      signin.style.display === "none" ||
      signin.style.opacity === 0 ||
      gotosignup.style.opacity == 0
    ) {
      logintopdiv.style.backgroundColor = "#6C51FE";
      innerlogindiv.style.backgroundColor = "#6048E4";
      loginbtn.style.backgroundColor = "#6C51FE";
      if (vw < 767) {
        gotosignup.style.transition = "opacity 1s";
        signin.style.transition = "opacity 1s , height 1s";
        signin.style.height = "70vh";
        signin.style.zIndex = 1;
        signin.style.padding = "5% 0";
        signin.style.opacity = 1;
        gotosignup.style.opacity = 1;
        gotosignup.style.height = "30vh";
        gotosignin.style.transition = "opacity 0s , height 1s";
        signup.style.transition = "opacity 0s , height 0.7s";
        gotosignin.style.padding = 0;
        signup.style.zIndex = -1;
        signup.style.padding = 0;
        signup.style.height = 0;
        signup.style.opacity = 0;
        gotosignin.style.opacity = 0;
        gotosignin.style.height = 0;
        signin.style.display = "flex";
        gotosignup.style.display = "flex";
      } else {
        gotosignup.style.transition = "opacity 1s";
        signin.style.transition = "opacity 1s , width 1s";
        signin.style.width = "40%";
        signin.style.opacity = 1;
        gotosignup.style.opacity = 1;
        gotosignup.style.width = "50%";
        gotosignin.style.transition = "opacity 0s , width 1s";
        signup.style.transition = "opacity 0s , width 0.7s";
        signup.style.width = 0;
        signup.style.opacity = 0;
        gotosignin.style.opacity = 0;
        gotosignin.style.width = 0;
        signin.style.display = "flex";
        gotosignup.style.display = "block";
      }
    } else {
      logintopdiv.style.backgroundColor = "#31A9F5";
      innerlogindiv.style.backgroundColor = "#2A96D7";
      registerbtn.style.backgroundColor = "#31A9F5";
      if (vw < 767) {
        gotosignin.style.transition = "opacity 1s";
        signup.style.transition = "opacity 1s , height 1s";
        signup.style.height = "70vh";
        signup.style.padding = "5% 0";
        gotosignin.style.padding = "0 0 5%";
        signup.style.opacity = 1;
        signup.style.zIndex = 1;
        gotosignin.style.opacity = 1;
        gotosignin.style.height = "30vh";
        gotosignup.style.transition = "opacity 0s , height 1s";
        signin.style.transition = "opacity 0s , height 0.7s";
        signin.style.height = 0;
        signin.style.zIndex = -1;
        signin.style.padding = 0;
        signin.style.opacity = 0;
        gotosignup.style.opacity = 0;
        gotosignup.style.height = 0;
        signup.style.display = "flex";
        gotosignin.style.display = "flex";
      } else {
        gotosignin.style.transition = "opacity 1s";
        signup.style.transition = "opacity 1s , width 1s";
        signup.style.width = "40%";
        signup.style.opacity = 1;
        gotosignin.style.opacity = 1;
        gotosignin.style.width = "50%";
        gotosignup.style.transition = "opacity 0s , width 1s";
        signin.style.transition = "opacity 0s , width 0.7s";
        signin.style.width = 0;
        signin.style.opacity = 0;
        gotosignup.style.opacity = 0;
        gotosignup.style.width = 0;
        signup.style.display = "flex";
        gotosignin.style.display = "block";
      }
    }
  };

  const savelogindetails = async (user) => {
    try {
      await axios.post("/", user);
    } catch (error) {
      console.log(error);
    }
  };

  const Register = (event) => {
    event.preventDefault();
    if (document.getElementById("paswd").style.color === "red") {
      alert(
        "Password must contain atleast 1 {Special character, Digit, Alphabet} & minimum length must be 6"
      );
    } else {
      if (signuppassword === confirmPassword) {
        if (document.getElementById("usremail").style.color === "red") {
          let emailid = document.getElementById("usremail");
          emailid.placeholder = "Email already exist!";
          emailid.classList.add("placeholderColor");
          emailid.focus();
          setSignupmail("");
        } else {
          props.func(true);
          navigate("/home");
          let user = {
            userName: name,
            email: signupemail,
            password: signuppassword,
          };
          savelogindetails(user);
          window.userEmail = signupemail;
        }
      } else {
        let pass = document.getElementById("confirmpass");
        pass.placeholder = "Password didn't match";
        pass.classList.add("placeholderColor");
        pass.focus();
        setConfirmPassword("");
      }
    }
  };

  const Login = async (event) => {
    event.preventDefault();
    let alreadyEmail = await axios.get(`/${loginemail}`);
    if (alreadyEmail.data.length === 0) {
      document.getElementById("usremailLogin").style.color = "red";
    } else {
      document.getElementById("usremailLogin").style.color = "green";
    }
    if (document.getElementById("usremailLogin").style.color === "green") {
      if (loginpassword === alreadyEmail.data[0].password) {
        props.func(true);
        navigate("/home");
        window.userEmail = loginemail;
      } else {
        let pass = document.getElementById("loginPassword");
        pass.placeholder = "Password didn't match";
        pass.classList.add("placeholderColor");
        pass.focus();
        setLoginpassword("");
      }
    } else {
      let emailid = document.getElementById("usremailLogin");
      emailid.placeholder = "Email didn't exist!";
      emailid.classList.add("placeholderColor");
      emailid.focus();
      setLoginupmail("");
    }
  };

  const signupemailHandler = async (e) => {
    setSignupmail(e.target.value);
    let val = e.target.value;
    let alreadyEmail = await axios.get(`/${val}`);
    if (alreadyEmail.data.length !== 0) {
      document.getElementById("usremail").style.color = "red";
    } else {
      document.getElementById("usremail").style.color = "green";
    }
  };

  const loginemailHandler = async (e) => {
    setLoginupmail(e.target.value);
    let val = e.target.value;
    let alreadyEmail = await axios.get(`/${val}`);
    if (alreadyEmail.data.length !== 0) {
      document.getElementById("usremail").style.color = "red";
    } else {
      document.getElementById("usremail").style.color = "green";
    }
  };

  const signupPassword = (e) => {
    setSignuppassword(e.target.value);
    console.log(e.target.value);
    let regularExpression =
      /^(?=.*\d)(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[A-Z]).{6,}$/;
    console.log(!regularExpression.test(signuppassword));
    if (!regularExpression.test(e.target.value)) {
      document.getElementById("paswd").style.color = "red";
    } else {
      document.getElementById("paswd").style.color = "green";
    }
  };

  return (
    <>
      <div id="logintopDiv">
        <div id="innerloginDiv">
          <div className="userdetails" id="gotoSignin">
            <h1>Already Have an Account ?</h1>
            <div>
              <button className="switchhere" onClick={switchMenu}>
                Sign in
              </button>
            </div>
          </div>
          <div className="switchbutton" id="signUp">
            <h1 style={{ marginTop: "0" }}>Sign up</h1>
            <form onSubmit={Register} className="loginform">
              <input
                name="userName"
                value={name}
                placeholder="USERNAME"
                type="text"
                onChange={(e) => setName(e.target.value)}
                required
                className="logininputs"
              />
              <input
                id="usremail"
                name="userEmail"
                value={signupemail}
                placeholder="EMAIL ADDRESS"
                type="email"
                onChange={signupemailHandler}
                required
                className="logininputs"
              />
              <input
                id="paswd"
                name="password"
                value={signuppassword}
                placeholder="PASSWORD"
                type="text"
                onChange={signupPassword}
                required
                className="logininputs"
              />
              <input
                id="confirmpass"
                name="confirmPassword"
                value={confirmPassword}
                placeholder="CONFIRM PASSWORD"
                type="password"
                onChange={(e) => setConfirmPassword(e.target.value)}
                required
                className="logininputs"
              />
              <div>
                <input
                  type="submit"
                  className="register"
                  value="REGISTER"
                  id="registerBtn"
                />
              </div>
            </form>
          </div>
          <div className="switchbutton" id="signIn">
            <h1 style={{ marginTop: "0" }}>Sign in</h1>
            <form onSubmit={Login} className="loginform">
              <input
                id="usremailLogin"
                name="userEmail"
                value={loginemail}
                placeholder="EMAIL ADDRESS"
                type="email"
                onChange={loginemailHandler}
                required
                className="logininputs"
              />
              <input
                id="loginPassword"
                name="password"
                value={loginpassword}
                placeholder="PASSWORD"
                type="text"
                onChange={(e) => setLoginpassword(e.target.value)}
                required
                className="logininputs"
              />
              <div>
                <input
                  type="submit"
                  className="register"
                  value="LOGIN"
                  id="loginBtn"
                />
              </div>
            </form>
          </div>
          <div className="userdetails" id="gotoSignup" onClick={switchMenu}>
            <h1>Don't Have an Account ?</h1>
            <div>
              <button className="switchhere">Sign up</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
