import React, { useState, useRef } from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend, Title } from "chart.js";
import { CanvasJSChart } from "canvasjs-react-charts";

ChartJS.register(ArcElement, Tooltip, Legend, Title);

export default function BodyStrength() {
  const [Pushup, setPushup] = useState("");
  const [Squat, setSquat] = useState("");
  const [Plank, setPlank] = useState("");
  const [result, setResult] = useState(false);
  const [gender, setGender] = useState("Male");
  const [output , setOutput] = useState(0);
  const ref = useRef(null);
  let pushupMarks = 0;
  let squatMarks = 0;
  let plankMarks = 0;

  const PlankCalcMale = () => {
    if (Plank < 20) {
      plankMarks=2.5;
    } else if (Plank >= 20 && Plank <= 45) {
      plankMarks=5.0;
    } else if (Plank >= 46 && Plank <= 90) {
      plankMarks=7.5;
    } else {
      plankMarks=10.0;
    }
  };

  const SquatCalcMale = () => {
    if (Squat < 20) {
      squatMarks=2.5;
      PlankCalcMale();
    } else if (Squat >= 20 && Squat <= 33) {
      squatMarks=5.0;
      PlankCalcMale();
    } else if (Squat >= 34 && Squat <= 50) {
      squatMarks=7.5;
      PlankCalcMale();
    } else {
      squatMarks=10.0;
      PlankCalcMale();
    }
  };

  const PlankCalcFemale = () => {
    if (Plank < 12) {
      plankMarks=2.5;
    } else if (Plank >= 12 && Plank <= 30) {
      plankMarks=5.0;
    } else if (Plank >= 31 && Plank <= 58) {
      plankMarks=7.5;
    } else {
      plankMarks=10.0
    }
  };

  const SquatCalcFemale = () => {
    if (Squat < 15) {
      squatMarks=2.5;
      PlankCalcFemale();
    } else if (Squat >= 15 && Squat <= 30) {
      squatMarks=5.0;
      PlankCalcFemale();
    } else if (Squat >= 31 && Squat <= 45) {
      squatMarks=7.5;
      PlankCalcFemale();
    } else {
      squatMarks=10.0;
      PlankCalcFemale();
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (gender === "Male") {
      if (Pushup < 11) {
        pushupMarks=2.5;
        SquatCalcMale();
      } else if (Pushup >=11  && Pushup <= 20) {
        pushupMarks=5.0;
        SquatCalcMale();
      } else if (Pushup >= 21 && Pushup <= 35) {
        pushupMarks=7.5;
        SquatCalcMale();
      } else {
        pushupMarks=10.0;
        SquatCalcMale();
      }
    } else {
      if (Pushup < 7) {
        pushupMarks=2.5;
        SquatCalcFemale();
      } else if (Pushup >= 7 && Pushup <= 15) {
        pushupMarks=5.0;
        SquatCalcFemale();
      } else if (Pushup >= 16 && Pushup <= 30) {
        pushupMarks=7.5;
        SquatCalcFemale();
      } else {
        pushupMarks=10.0;
        SquatCalcFemale();
      }
    }
    let score = ((pushupMarks + plankMarks + squatMarks) / 30) * 100;
    score = score.toFixed();
    setOutput(score);
    setResult(true);
    ref.current?.scrollIntoView({ behavior: "smooth" });
  };

  const option = {
    animationEnabled: true,
    animationDuration: 2000,
    theme: "light2",
    title: {
      text: "Body Strength",
    },
    subtitles: [
      {
        text: `${output}% Positive`,
        verticalAlign: "center",
        fontSize: 18,
        dockInsidePlotArea: true,
      },
    ],
    data: [
      {
        type: "doughnut",
        radius: "100%",
        innerRadius: "50%",
        showInLegend: true,
        indexLabel: "{name}: {y}",
        dataPoints: [
          { name: "Push-ups in 1 minute", y: Pushup },
          { name: "Squat in 1 minute", y: Squat },
          { name: "Plank pose no. of sec", y: Plank },
        ],
      },
    ],
  };

  return (
    <>
      <div className="subservice">
        <h1 className="serviceheading">Body Strength</h1>
        <div className="subserviceBody">
          <div className="smalldiv">
            <img
              src={require("./img/pushup.gif")}
              style={{ float: "left", margin: "0 5% 0 0", width: "100%" }}
              alt="Body mass index"
            />
          </div>
          <div className="bigdiv">
            <p style={{ marginBottom: "5%", textAlign: "initial" }}>
              <span style={{ fontWeight: "bolder", fontSize: "xx-large" }}>
                Push-Ups
              </span>{" "}
              are a bodyweight exercise that targets the chest, shoulders, and
              triceps.Push-ups engage core muscles and can be modified for
              different fitness levels. They are a versatile and effective
              exercise for building upper body strength and can be done anywhere
              without any equipment.
              <br />
              <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                To perform a push-up
              </span>{" "}
              start in a high plank position with hands shoulder-width apart and
              feet together. Lower your body by bending your elbows until your
              chest nearly touches the floor. Push back up to the starting
              position by extending your arms. Keep your body straight
              throughout the movement. Regular push-ups help improve overall
              upper body muscle tone and endurance.
            </p>
          </div>
        </div>
        <div className="subserviceBody1">
          <div className="bigdiv">
            <p style={{ marginBottom: "5%", textAlign: "initial" }}>
              <span style={{ fontWeight: "bolder", fontSize: "xx-large" }}>
                Squat{" "}
              </span>
              exercise is a fundamental compound movement that targets the lower
              body muscles, including the quadriceps, hamstrings, glutes, and
              calves. It is performed by bending the knees and hips, lowering
              the body into a squat position, and then rising back up. Squats
              can be done with bodyweight or added resistance like dumbbells or
              barbells. They offer benefits such as improved lower body
              strength, muscle development, and enhanced functional movement.
              <br />
              <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                To perform a squat
              </span>{" "}
              start with your feet slightly wider than shoulder-width apart,
              toes pointed slightly outward. Lower your body by bending your
              knees and hips, keeping your chest lifted and weight on your
              heels.
              <br />
              Descend until your thighs are parallel to the ground, and then
              push through your heels to return to the starting position.
            </p>
          </div>
          <div className="smalldiv">
            <img
              src={require("./img/squad.gif")}
              style={{ float: "left", margin: "0 5% 0 0", width: "100%" }}
              alt="Body mass index"
            />
          </div>
        </div>
        <div className="subserviceBody">
          <div className="smalldiv">
            <img
              src={require("./img/planks.gif")}
              style={{ float: "left", margin: "0 5% 0 0", width: "100%" }}
              alt="Body mass index"
            />
          </div>
          <div className="bigdiv">
            <p style={{ marginBottom: "5%", textAlign: "initial" }}>
              <span style={{ fontWeight: "bolder", fontSize: "xx-large" }}>
                Plank{" "}
              </span>
              exercise is a popular bodyweight exercise that targets the core
              muscles, including the abdominals, obliques, and lower back.
              <br />
              <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                To perform a plank
              </span>{" "}
              start in a push-up position, with your forearms resting on the
              ground and your elbows directly below your shoulders. Engage your
              core and keep your body in a straight line from head to heels.
              Hold this position for as long as you can while maintaining proper
              form and breathing steadily. Planks are effective for building
              core strength, stability, and improving posture.
            </p>
          </div>
        </div>
      </div>
      <section className="formsect">
        <div className="servicesForm">
          <div className="formheading">
            <h2>Please Enter Correct Data For Accurate Results</h2>
          </div>
          <form onSubmit={handleSubmit} className="bmiform">
            <div
              style={{
                boxSizing: "border-box",
                width: "100%",
                textAlign: "center",
                padding: "0 5%",
              }}
            >
              <div className="formbody">
                <div className="inputfields">
                  <div style={{ width: "50%" }}>
                    <label style={{ paddingRight: "3%" }}> :</label>
                  </div>
                  <div style={{ width: "50%" }}>
                    <input
                      placeholder="No. of pushups in 1min"
                      name="Pushup"
                      className="bmiinputs"
                      type="number"
                      min={0}
                      max={200}
                      required
                      value={Pushup}
                      onChange={(e) => setPushup(e.target.value)}
                    />
                  </div>
                </div>
                <div style={{ width: "10%" }}></div>
                <div className="inputfields">
                  <div style={{ width: "50%" }}>
                    <label style={{ paddingRight: "3%" }}> :</label>
                  </div>
                  <div style={{ width: "50%" }}>
                    <input
                      placeholder="No. of squat in 1min"
                      name="Squat"
                      className="bmiinputs"
                      type="number"
                      min={0}
                      max={300}
                      required
                      value={Squat}
                      onChange={(e) => setSquat(e.target.value)}
                    />
                  </div>
                </div>
              </div>
              <div className="inputfield">
                <div className="inputfields">
                  <div style={{ width: "50%" }}>
                    <label style={{ paddingRight: "3%" }}>  :</label>
                  </div>
                  <div style={{ width: "50%" }}>
                    <input
                      placeholder="No. of seconds"
                      name="Plank"
                      className="bmiinputs"
                      type="number"
                      min={5}
                      max={400}
                      required
                      value={Plank}
                      onChange={(e) => setPlank(e.target.value)}
                    />
                  </div>
                </div>
              </div>
              <div className="gender">
                <div className="genderinput">Gender :</div>
                <div className="maleinp">
                  <input
                    type="radio"
                    id="Male"
                    name="Sex"
                    value="Male"
                    onChange={(e) => setGender(e.target.value)}
                    required
                  />
                  <label htmlFor="Male"> </label>
                </div>
                <div className="femaleinp">
                  <input
                    type="radio"
                    id="Female"
                    name="Sex"
                    value="Female"
                    onChange={(e) => setGender(e.target.value)}
                    required
                    style={{ marginRight: "5%" }}
                  />
                  <label htmlFor="Female"></label>
                </div>
              </div>
              <div>
                <div className="imput">
                  <a href="#resultbf">
                    <input
                      type="submit"
                      className="calculatebtn"
                      value={""}
                    />
                  </a>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div ref={ref}></div>
      </section>
      {result && (
        <div className="resultsection">
          <div className="resultText">
            <p
              style={{
                marginBottom: "5%",
                textAlign: "initial",
                fontSize: "large",
              }}
            >
              <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                Based on the data you provided
              </span>
              , including the number of push-ups, squats, plank time, and your
              gender.
              <br /> We have calculated your Body Strength value. This value
              reflects your overall strength and muscular endurance.
              <br />
              After analyzing your data, your Body Strength score
              is determined to be{" "}
              <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                {output}% Positive
              </span>
              .
            </p>
            <img
              src={require("./img/w11.png")}
              id="strengthImg"
              alt="Body mass index"
            />
          </div>
          <div id="strengthGraph">
            <div>
              <CanvasJSChart
                options={option}
                /* onRef={ref => this.chart = ref} */
              />
              {/*You can get reference to the chart instance as shown above using onRef. This allows you to access all chart properties and methods*/}
            </div>
          </div>
        </div>
      )}
    </>
  );
}
