import React from "react";
import { NavLink, useNavigate } from "react-router-dom";
import "../info.css";
import { useState, useEffect } from "react";
import axios from "axios";

const Navbar = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [showmenu, setShowmenu] = useState(true);
  const [hidemenu, setHidemenu] = useState(false);
  const navigate = useNavigate();

  const username = async () => {
    let user = await axios.get(`/${window.userEmail}`);
    let usr = user.data[0].userName;
    usr = usr.charAt(0).toUpperCase() + usr.slice(1);
    document.getElementById("usrname").innerText = usr;
  };

  useEffect(() => {
    username();
    window.onresize = function () {
      checkWidth();
    };
    checkWidth();

    function checkWidth() {
      var vw = Math.max(
        document.documentElement.clientWidth || 0,
        window.innerWidth || 0
      );
      let menu = document.getElementById("showmenu");
      if (vw < 767) {
        let next1 = menu.nextSibling;
        next1.style.display = "none";
        let next2 = next1.nextSibling;
        next2.style.display = "none";
        let next3 = next2.nextSibling;
        next3.style.display = "none";
        let next4 = next3.nextSibling;
        next4.style.display = "none";
        let next5 = next4.nextSibling;
        next5.style.display = "none";
        let next6 = next5.nextSibling;
        next6.style.display = "none";
      } else {
        let next1 = menu.nextSibling;
        next1.style.display = "block";
        let next2 = next1.nextSibling;
        next2.style.display = "block";
        let next3 = next2.nextSibling;
        next3.style.display = "block";
        let next4 = next3.nextSibling;
        next4.style.display = "block";
        let next5 = next4.nextSibling;
        next5.style.display = "block";
        let next6 = next5.nextSibling;
        next6.style.display = "block";
      }
    }
  }, []);

  const showallmenu = (e) => {
    let menu = document.getElementById("showmenu");
    let next1 = menu.nextSibling;
    if (next1.style.display === "none") {
      setShowmenu(false);
      setHidemenu(true);
      next1.style.display = "block";
      let next2 = next1.nextSibling;
      next2.style.display = "block";
      let next3 = next2.nextSibling;
      next3.style.display = "block";
      let next4 = next3.nextSibling;
      next4.style.display = "block";
      let next5 = next4.nextSibling;
      next5.style.display = "block";
      let next6 = next5.nextSibling;
      next6.style.display = "flex";
      window.scrollTo(0,0, document.body.scrollHeight);
    } else {
      setShowmenu(true);
      setHidemenu(false);
      let next1 = menu.nextSibling;
      next1.style.display = "none";
      let next2 = next1.nextSibling;
      next2.style.display = "none";
      let next3 = next2.nextSibling;
      next3.style.display = "none";
      let next4 = next3.nextSibling;
      next4.style.display = "none";
      let next5 = next4.nextSibling;
      next5.style.display = "none";
      let next6 = next5.nextSibling;
      next6.style.display = "none";
    }
  };

  const onMouseOverCaptureHandler = (e) => {
    e.target.style.cursor = "pointer";
    if (e.target.children[0]) {
      e.target.children[0].style.color = "white";
      e.target.children[0].style.transition = "color 0.5s";
    }
  };
  const onMouseOutCaptureHandler = (e) => {
    if (e.target.children[0]) {
      e.target.children[0].style.color = "black";
    }
  };

  const navigation = (e) => {
    if (e.target.children[0]) {
      navigate(e.target.children[0].getAttribute("href"));
      // console.log(e.target.children[0].getAttribute("href"));
    }
  };

  const listenToScroll = () => {
    let heightToHideFrom = 500;
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop;

    if (winScroll > heightToHideFrom) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", listenToScroll);
  }, []);

  return (
    <>
      <div id="subsection"></div>
      <div className="navbar">
        <ul id="navul">
          <li
            className="logonav navWidth"
            id="homepagelogo"
            onMouseOver={onMouseOverCaptureHandler}
            onMouseOut={onMouseOutCaptureHandler}
            onClick={navigation}
          >
            <NavLink
              to="/home"
              style={({ isActive }) =>
                isActive
                  ? {
                      borderBottom: "3px solid rgb(110, 89, 198)",
                      paddingBottom: "0.7vh",
                    }
                  : {}
              }
            >
              
            </NavLink>
          </li>
          <li id="showmenu" onClick={showallmenu}>
            {hidemenu && (
              <i
                className="fa fa-times"
                style={{ fontSize: "46px", color: "red" }}
              ></i>
            )}
            {showmenu && (
              <i
                className="fa fa-reorder"
                style={{ fontSize: "48px", color: "tomato" }}
              ></i>
            )}
          </li>
          <li
            className="nav navWidth"
            onMouseOver={onMouseOverCaptureHandler}
            onMouseOut={onMouseOutCaptureHandler}
            onClick={navigation}
          >
            <NavLink
              to="/about"
              style={({ isActive }) =>
                isActive
                  ? {
                      borderBottom: "3px solid rgb(110, 89, 198)",
                      paddingBottom: "0.8vh",
                    }
                  : {}
              }
            >
              ABOUT
            </NavLink>
          </li>
          <li
            className="nav navWidth"
            onMouseOver={onMouseOverCaptureHandler}
            onMouseOut={onMouseOutCaptureHandler}
            onClick={navigation}
          >
            <NavLink
              to="/services"
              style={({ isActive }) =>
                isActive
                  ? {
                      borderBottom: "3px solid rgb(110, 89, 198)",
                      paddingBottom: "0.8vh",
                    }
                  : {}
              }
            >
              SERVICES
            </NavLink>
          </li>
          <li
            className="nav navWidth"
            onMouseOver={onMouseOverCaptureHandler}
            onMouseOut={onMouseOutCaptureHandler}
            onClick={navigation}
          >
            <NavLink
              to="/facilities"
              style={({ isActive }) =>
                isActive
                  ? {
                      borderBottom: "3px solid rgb(110, 89, 198)",
                      paddingBottom: "0.8vh",
                    }
                  : {}
              }
            >
              FACILITIES
            </NavLink>
          </li>
          <li
            className="nav navWidth"
            onMouseOver={onMouseOverCaptureHandler}
            onMouseOut={onMouseOutCaptureHandler}
            onClick={navigation}
          >
            <NavLink
              to="/membership"
              style={({ isActive }) =>
                isActive
                  ? {
                      borderBottom: "3px solid rgb(110, 89, 198)",
                      paddingBottom: "0.8vh",
                    }
                  : {}
              }
            >
              MEMBERSHIP
            </NavLink>
          </li>
          <li style={{ width: "22%" }} id="rightemptyli"></li>
          <li className="usernav" id="user">
            <div id="usersection">
              <a href="#" id="usericonpos">
                <i className="fa fa-user" id="usricon"></i>
              </a>
              <a href="#" id="usrname">
                Sachin
              </a>
            </div>
          </li>
        </ul>
      </div>
      {isVisible && (
        <div style={{ float: "right" }}>
          <div className="gotoTop">
            <a href="#subsection">
              <img
                src={require("./img/top1.png")}
                alt="go to top"
                style={{ borderRadius: "60px", width: "30%" }}
              />
            </a>
          </div>
        </div>
      )}
    </>
  );
};

export default Navbar;
