import React from "react";
import { useState, useRef } from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend, Title } from "chart.js";
import { CanvasJSChart } from "canvasjs-react-charts";

ChartJS.register(ArcElement, Tooltip, Legend, Title);

export default function Bmi() {
  const [Height, setHeight] = useState("");
  const [Weight, setWeight] = useState("");
  const [output, setOutput] = useState(0);
  const [result, setResult] = useState(false);
  const [gender, setGender] = useState("Male");
  const [category, setCategory] = useState("Normal");
  const ref = useRef(null);
  let heightInmeter = Height / 100;
  let value = Weight / (heightInmeter * heightInmeter);
  if(gender==="Female") {
    value+=0.7;
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    if (gender === "Male") {
      setOutput(value);
    } else {
      setOutput(value);
    }
    if (value < 18.5) {
      setCategory("Under Weight");
    } else if (value >= 18.5 && value <= 24.9) {
      setCategory("Normal");
    } else if (value >= 25 && value <= 29.9) {
      setCategory("Over Weight");
    } else if (value >= 30 && value <= 34.9) {
      setCategory("Obese");
    } else {
      setCategory("Extremely Obese");
    }

    setResult(true);
    ref.current?.scrollIntoView({ behavior: "smooth" });
  };

  const options = {
    animationEnabled: true,
    animationDuration: 1200,
    theme: "light2",
    title: {
      text: "YOUR BODY MASS INDEX ",
      fontFamily: "helvetica",
    },
    subtitles: [
      {
        text: "BASED ON YOUR ENTERED DATA",
        fontFamily: "helvetica",
      },
    ],
    axisY: {
      title: "Body Mass Index Values",
      lineThickness: 4,
    },
    data: [
      {
        type: "rangeBar",
        indexLabel: "{y[#index]}",
        indexLabelOrientation: "vertical",
        indexLabelFontSize: 18,
        indexLabelFontColor: "Gray",
        dataPoints: [
          { label: "Normal BMI", y: [18.5, 25] },
          { label: "Your BMI", y: [Math.floor(output), Math.ceil(output)] },
        ],
      },
    ],
  };

  return (
    <>
      <div className="subservice">
        <h1 className="serviceheading">BMI</h1>
        <div className="subserviceBody">
          <div id="bmiimg1">
            <img
              src={require("./img/s8.png")}
              style={{ float: "left", margin: "0 5% 0 0", width: "100%" }}
              alt="Body mass index"
            />
          </div>
          <div id="bmiimgtext1">
            <p style={{ marginBottom: "5%", textAlign: "initial" }}>
              <span style={{ fontWeight: "bolder", fontSize: "xx-large" }}>
                BMI{" "}
              </span>
              stands for Body Mass Index. It is a measure that helps assess the
              relationship between a person's weight and height, providing an
              estimation of their body composition.
              <br />
              BMI is commonly used as a screening tool to determine whether an
              individual falls within a healthy weight range or if they may be
              underweight, overweight, or obese.
            </p>
          </div>
        </div>
      </div>
      <section className="formsect">
        <div className="servicesForm">
          <div className="formheading">
            <h2>Please Enter Correct Data For Accurate Results</h2>
          </div>
          <form onSubmit={handleSubmit} className="bmiform">
            <div
              style={{
                boxSizing: "border-box",
                width: "100%",
                textAlign: "center",
                padding: "0 5%",
              }}
            >
              <div className="formbody">
                <div className="inputfields">
                  <div style={{ width: "50%" }}>
                    <label style={{ paddingRight: "3%" }}> () :</label>
                  </div>
                  <div style={{ width: "50%" }}>
                    <input
                      placeholder="Enter your height"
                      name="Height"
                      className="bmiinputs"
                      type="number"
                      min={20}
                      max={250}
                      required
                      value={Height}
                      onChange={(e) => setHeight(e.target.value)}
                    />
                  </div>
                </div>
                <div style={{ width: "10%" }}></div>
                <div className="inputfields">
                  <div style={{ width: "50%" }}>
                    <label style={{ paddingRight: "3%" }}> () :</label>
                  </div>
                  <div style={{ width: "50%" }}>
                    <input
                      placeholder="Enter your weight"
                      name="Weight"
                      className="bmiinputs"
                      type="number"
                      min={1}
                      max={400}
                      required
                      value={Weight}
                      onChange={(e) => setWeight(e.target.value)}
                    />
                  </div>
                </div>
              </div>
              <div className="gender">
                <div className="genderinput">Gender :</div>
                <div className="maleinp">
                  <input
                    type="radio"
                    id="Male"
                    name="Sex"
                    value="Male"
                    onChange={(e) => setGender(e.target.value)}
                    required
                  />
                  <label htmlFor="Male"> </label>
                </div>
                <div className="femaleinp">
                  <input
                    type="radio"
                    id="Female"
                    name="Sex"
                    value="Female"
                    onChange={(e) => setGender(e.target.value)}
                    required
                    style={{ marginRight: "5%" }}
                  />
                  <label htmlFor="Female"></label>
                </div>
              </div>
              <div>
                <div>
                  <a href="#resultbf">
                    <input
                      type="submit"
                      className="calculatebtn"
                      value={""}
                    />
                  </a>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div ref={ref}></div>
      </section>
      {result && (
        <div>
          <div className="resultsection">
            <div className="resultText">
              <p
                style={{
                  marginBottom: "5%",
                  textAlign: "initial",
                  fontSize: "large",
                }}
              >
                <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                  Based on the data you provided
                </span>
                , We have calculated your Body Mass Index (BMI) value, which
                serves as an indicator of your body fat based on your height,
                weight, and gender.
                <br />
                Your BMI value is <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                   {value.toFixed(2)} .
                </span>
                <br />
                This value falls within the <span style={{ fontWeight: "bolder", fontSize: "x-large" }}>
                  {category}
                </span> category, according to the standard BMI classifications.
              </p>
              <img
                src={require("./img/bbn1.png")}
                style={{ float: "left", margin: "0 5% 0 0", width: "100%" }}
                alt="Body mass index"
              />
            </div>
            <div className="resultGraph">
              <div>
                <CanvasJSChart options={options} />
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
