import React from "react";
import { useState } from "react";

export default function Slider(props) {
  const [disabled,usedisabled] = useState(true);
  const [img0, setimg0] = useState(props.Images[0]);
  const [img1, setimg1] = useState(props.Images[1]);
  const [img2, setimg2] = useState(props.Images[2]);
  const imgarray = props.Images;
  const index = imgarray.findIndex((img) => img === img0);

  const previousImg = () => {
    if(index===1){
      usedisabled(true);
    }
    setimg0(imgarray[(index-1) % 5]);
    setimg1(imgarray[(index) % 5]);
    setimg2(imgarray[(index+1) % 5]);
  };

  const nextImg = () => {
    if (index === 0) {
      usedisabled(false);
    }
    if(index===4) {
      usedisabled(true);
    }
    setimg0(imgarray[(index + 1) % 5]);
    setimg1(imgarray[(index + 2) % 5]);
    setimg2(imgarray[(index + 3) % 5]);
  };

  return (
    <>
      <div className="middlesection">
        <div className="slider">
          <div className="slidertitle">
            <h1 style={{ margin: "4% 0 0", padding: "5% 0 1%" }}>
              {props.Title}
            </h1>
          </div>
          <div className="sliderdesc">
            <p style={{ width: "60%", padding: "0 20%" }}>
              {props.Description}
            </p>
          </div>
        </div>
        <div id="sliderimg">
          <div
            style={{
              width: "3.5vw",
              position: "relative",
              boxSizing: "border-box",
            }}
          >
            <button
              className="symbol"
              onClick={previousImg}
              id="Prevbtn"
              disabled={disabled}
            >
              &lt;
            </button>
          </div>
          <div className="sliderrimg">
            <img src={require(`${img0}`)} alt="" className="sliderimages" />
          </div>
          <div style={{ width: "1.5vw" }}></div>
          <div className="sliderrimg">
            <img src={require(`${img1}`)} alt="" className="sliderimages" />
          </div>
          <div style={{ width: "1.5vw" }}></div>
          <div className="sliderrimg">
            <img src={require(`${img2}`)} alt="" className="sliderimages" />
          </div>
          <div
            style={{
              width: "3.5vw",
              position: "relative",
              boxSizing: "border-box",
              overflow: "hidden",
            }}
          >
            <button className="symbol" onClick={nextImg} id="Nextbtn">
              &gt;
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
